import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { Team } from './team.interface';

@Injectable({
	providedIn: 'root'
})

export class EsportService {

	baseURL: string = 'https://tmf-esport.herokuapp.com/teams';
	// baseURL: string = 'http://localhost:8080/teams';
  // headers = new HttpHeaders().set("Content-Type", "application/json");

	constructor(private HTTP: HttpClient) { }

	// GET ALL TEAMS
  getTeams() {
    return this.HTTP.get(this.baseURL);
  }

	// POST Team
  addTeam(name: string, contact:string, address: string, logoTeam: File)
	: Observable<any> {
    var formData: any = new FormData();
    formData.append("name", name);
    formData.append("contact", contact);
    formData.append("address", address);
    formData.append("logo", logoTeam);

    return this.HTTP.post<Team>(`${this.baseURL}/register`, formData, {
      reportProgress: true,
      observe: "events"
    });
  }

}
