import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';

import { IsLoadingService } from '@service-work/is-loading';
import { EsportService } from '../../shared/esport.service';

@Component({
  selector: 'app-matches',
  templateUrl: './matches.component.html',
  styleUrls: ['./matches.component.scss']
})

export class MatchesComponent implements OnInit {

  DATA_SOURCE: any = [];
  displayedColumns = ['logo', 'name', 'address', 'score', 'action'];

  constructor(
    private esportService: EsportService,
    private isLoadingService: IsLoadingService
  ) { }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

 
  ngOnInit() {
    // GET all teams onInit
    this.esportService.getTeams().subscribe(res => {
      let Teams = res['teams'].map(team => {
        return {
          name: team.name,
          address: team.address,
          score: team.score,
          logo: team.logo
         }
      });

      this.DATA_SOURCE = new MatTableDataSource(Teams);
      this.DATA_SOURCE.sort = this.sort;
      this.DATA_SOURCE.paginator = this.paginator;
    });

    this.isLoadingService.add(this.esportService.getTeams());
  }

  // Filtering on (keyup)
  applyFilter(filterValue: string) {
    this.DATA_SOURCE.filter = filterValue.trim().toLowerCase();
  }

  // Edit score
  editScore(team) {
    console.log('TODO function')
  }

}